  padding-top: 26px;
  padding-bottom: 28px;
}
@media (max-width: 767px) {
  .footer {
    padding-bottom: 45px;
  }
}

.footer__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  padding-left: 40px;
  padding-right: 40px;
  max-width: 1920px;
  min-width: 375px;
  margin: 0 auto;
}
@media (max-width: 959px) {
  .footer__wrapper {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 10px;
    padding-left: 15px;
    padding-right: 15px;
    row-gap: 10px;
  }
}

.footer__top {
  grid-column: 1/2;
  justify-self: start;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  background-color: #ffffff;
  border-radius: 50%;
  transition: transform 0.3s ease;
  margin-top: 20px;
}
.footer__top:hover {
  transform: scale(1.1);
}
.footer__top:focus {
  transform: scale(1.1);
}
@media (max-width: 959px) {
  .footer__top {
    grid-row: 1/2;
    width: 54px;
    height: 54px;
    margin-top: 0;
  }
}

.footer__container {
  grid-column: 2/4;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
@media (max-width: 959px) {
  .footer__container {
    grid-column: 1/5;
    grid-row: 1/2;
    align-items: center;
    width: 210px;
    margin-left: auto;
    margin-right: auto;
    gap: 13px;
  }
}

.footer__logo {
  justify-content: start;
  margin-bottom: 10px;
}
@media (max-width: 767px) {
  .footer__logo {
    margin-bottom: 8px;
  }
}

.footer__copyright {
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  color: #cfcfcf;
  margin-top: 0;
  margin-bottom: 0;
}
@media (max-width: 767px) {
  .footer__copyright {
    text-align: center;
  }
}

.footer__link {
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 14px;
  line-height: 15px;
  font-weight: 500;
  transition: opacity 0.3s ease;
  margin-top: 0;
  margin-bottom: 0;
  text-decoration: underline;
  text-decoration-color: rgba(255, 255, 255, 0.2);
}
.footer__link:hover {
  opacity: 0.6;
}
.footer__link:focus {
  opacity: 0.6;
}
.footer__link:active {
  opacity: 0.3;
}

.footer__nav {
  grid-column: 4/13;
  margin-left: 65px;
  display: flex;
}
@media (max-width: 959px) {
  .footer__nav {
    display: none;
  }
}

.nav-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  justify-content: start;
  flex-wrap: wrap;
  gap: 40px;
}

.nav-list__item {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: start;
  padding-left: 0;
}
@media (max-width: 959px) {
  .nav-list__item {
    width: 100%;
  }
}

.nav-list__link {
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  transition: opacity 0.3s ease;
}
.nav-list__link:hover {
  opacity: 0.6;
}
.nav-list__link:focus {
  opacity: 0.6;
}
.nav-list__link:active {
  opacity: 0.3;
}
@media (max-width: 959px) {
  .nav-list__link {
    padding-top: 16px;
    padding-bottom: 16px;
    width: 100%;
    text-align: center;
  }
}

